.Container{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 9vh;
}

.mainTitle{
    padding: 2rem;
    height: 150px;
    background-image: url(../../../public/pattern-circles.svg);
    background-repeat: no-repeat;
    background-position: center;
    text-align: center;
    line-height: 120%
}

.mainTitle h1 {
    color: hsl(227, 35%, 25%);
    font-size: 28px;
}

.signContent{
    color:hsl(225, 20%, 60%);
    font-weight: 600;
}

.cardLayout{
    margin-top: 3rem;
    background-color: hsl(0, 0%, 100%);
    width: 540px;
    height: 400px;
    box-shadow: 0px 22px 22px rgba(0,0,0,0.2);
    border-radius: 12px;
    transition: 0.3s ;
}

.cardLayout:hover{
    box-shadow: 0px 30px 40px rgba(0,0,0,0.3);
}

.sliderPrice{
    text-align: center;
}

.slider{
    -webkit-appearance: none;
    cursor: pointer;
    appearance: none;
    width: 440px;
    background: transparent;
}

.slider:active{
    cursor: grabbing;
}



/***** Chrome, Safari, Opera, and Edge Chromium *****/
input[type="range"]::-webkit-slider-runnable-track {
    background: hsl(224, 65%, 95%);
    height: 0.6rem;
    border-radius: 25px;
  }
  
  /******** Firefox ********/
  input[type="range"]::-moz-range-track {
    background: hsl(224, 65%, 95%);
    height: 0.6rem;
    border-radius: 25px;
  }

/***** Thumb Styles *****/
/***** Chrome, Safari, Opera, and Edge Chromium *****/
input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none; /* Override default look */
    appearance: none;
    margin-top: -12px; /* Centers thumb on the track */
    background-color: hsl(174, 86%, 45%);
    height: 2.5rem;
    width: 2.5rem;
    box-shadow: 0 16px 12px 4px hsl(174, 77%, 80%);
    background-image: url(../../../public/icon-slider.svg); 
    background-size: 60%;
    background-repeat: no-repeat;
    background-position: center;
 }

/***** Thumb Styles *****/
/***** Firefox *****/
input[type="range"]::-moz-range-thumb {
    border: none; /*Removes extra border that FF applies*/
    border-radius: 100%; /*Removes default border-radius that FF applies*/
    background-color: hsl(174, 86%, 45%);
    height: 2.5rem;
    width: 2.5rem;
    box-shadow: 0 12px 12px 9px hsl(174, 77%, 80%);
    background-image: url(../../../public/icon-slider.svg); 
    background-size: 60%;
    background-repeat: no-repeat;
    background-position: center;

}

input[type="range"]::-moz-range-progress {
background: hsl(174, 77%, 80%);
height: 0.5rem;
border-radius: 25px;
}

input[type="range"]:hover::-moz-range-thumb {
    background-color: hsl(174, 72%, 66%);
}

input[type="range"]:hover::-webkit-slider-thumb  {
    background-color: hsl(174, 72%, 66%);
}


input[type="range"]:active::-moz-range-thumb {
    background-color: hsl(174, 48%, 39%);
}

input[type="range"]:active::-webkit-slider-thumb  {
    background-color: hsl(174, 48%, 39%);
}


.price-container{
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 2.6rem 2.8rem 2rem 2.8rem;
    color:hsl(225, 20%, 60%);
    font-weight: 800;
}

.views{
    text-transform: uppercase;
    letter-spacing: 1.5px;
    
}

.priceTag{
    font-size: 40px;
    color:hsl(227, 35%, 25%);
}

.perMonth{
    display: flex;
    align-items: center;
}

.month{
    font-weight: 600;
    padding-left: 0.5rem;
}


.switch {
    position: relative;
    display: inline-block;
    width: 42px;
    height: 22px;
    margin-left: 1rem;
    margin-right: 1rem;
  }
  
  .switch input { 
    opacity: 0;
    width: 0;
    height: 0;
    outline: none;
  }
  
  .switcher {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .switcher:before {
    position: absolute;
    content: "";
    height: 14px;
    width: 14px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  input:hover + .switcher {
    background-color: hsl(174, 77%, 80%);
  }

  input:focus + .switcher {
    box-shadow: 0 0 1px hsl(174, 48%, 39%);
  }
  
  input:checked + .switcher:before {
    -webkit-transform: translateX(18px);
    -ms-transform: translateX(18px);
    transform: translateX(18px);
  }
  
  /* Rounded sliders */
  .switcher.round {
    border-radius: 34px;
  }
  
  .switcher.round:before {
    border-radius: 50%;
  }

  .switcher-container{
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      margin-top: 3rem;
      padding-left: 5.5rem;
      font-size: 12px;
      color: hsl(225, 20%, 60%);
  }

  .discount{
      margin-left: 0.5rem;
      background-color: hsl(14, 92%, 95%);
      color:hsl(15, 100%, 70%);
      border-radius: 25px;
      font-size: 10px;
      padding: 0.2rem 0.4rem;
      font-weight: 800;
  }

  hr {
      margin-top: 2.5rem;
      border:none;
      color: hsla(225, 20%, 60%, 0.801);
      background-color: hsla(222, 49%, 87%, 0.801);
      height: 1px;
  }

  .checks{
    color: hsl(225, 20%, 60%);
    font-size: 12px;
    padding: 1.5rem 3rem;
    line-height: 1.6rem;
  }

  .checks img {
      margin-right: 1rem;
  }

  .trialBtn {
      background-color: hsl(227, 35%, 25%);
      border-radius: 25px;
      padding: 0.8rem 2.8rem;
      color: hsl(225, 20%, 60%);
      font-weight: 800;
      border: none;
      margin-right: 2.5rem;
      transition: 0.2s;
  }

  .trialBtn:hover{
      cursor: pointer;
      color: hsl(0, 0%, 100%);
     
  }

  .trialBtn:active{
    transform: scale(90%);
}


  .bottomContent{
      display: flex;
      align-items: center;
      justify-content: space-between;
  }


  .mobile{
      display: none;
  }

  @media screen and (max-width: 560px) {
 
    .Container{
        margin-top: 3rem;
    }

    .mainTitle{
        padding: 2rem;
        height: 150px;
        background-image: url(../../../public/pattern-circles.svg);
        background-repeat: no-repeat;
        background-position: center;
        text-align: center;
        line-height: 120%
    }
    
    .mainTitle h1 {
        font-size: 20px;
    }
    
    .signContent{
        width: 200px;
        margin: auto;
    }

    .cardLayout{
        margin: 2rem 0 4rem 0;
        width: 90%;
        height: auto;
    }

    .slider{
        width: 90%;
       
    }

    .desktop{
        display: none;
    }

    .mobile{
        display: block;
    }

    .views{
        margin: auto;
        font-size: 12px;
    }

    .priceTag{
        font-weight: 800;
        font-size: 32px;
    }
    .smallSize{
        margin-top: 2rem;
        display: flex;
        justify-content: center;
        
    }

    .month{
        color:hsl(225, 20%, 60%);
    }

    .switcher-container{
        margin-top: 2rem;
        margin-left: 0;
        padding-left: 2rem;
    }

    .bottomContent{
        display: flex;
        flex-direction: column;
        margin-bottom: 2rem;
        text-align: center;
    }

    .trialBtn{
        margin: auto;
    }
  }